<template>
  <div
    class="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-blueGray-100 border-0"
  >
    <div class="flex-auto px-4 lg:px-10 py-10 pt-0">
      <form>
        <h6 class="text-blueGray-400 text-sm mt-3 mb-6 font-bold uppercase">
          Choose your suppliers
        </h6>
<!--        <div v-for="(vendor, index) in vendors" :key="index" class="option-block">
          <button @click.prevent="selectVendor(vendor.name)" class="option-button border border-black">{{ vendor.name }}</button>
        </div>-->
        <div>

          <div>
            <table>
      <thead>
        <tr>
          <th>Supplier</th>
          <th>Service Type</th>
          <th>Services</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(vendor, index) in vendors" :key="index">
<!--          <td>
            <img src="https://res.cloudinary.com/vidmax/image/upload/v1682437686/Logo_-_Comsol_lca4ks.png" alt="Provider Logo">
          </td>-->
          <td>{{ vendor.name }}</td>
          <td>
            <select v-model="vendor.serviceType" @change="loadServices(vendor)">
              <option value="">Select Service Type</option>
              <option v-for="serviceType in vendor.serviceTypes" :value="serviceType.id">{{ serviceType.name }}</option>
            </select>
          </td>
          <td>
            <div v-for="service in vendor.services" :key="service.id">
              <label>
                <input type="checkbox" v-model="service.selected">
                {{ service.name }}
              </label>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>


        </div> 
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      vendors: [],
      serviceTypes: []
    };
  },

  mounted() {
    //this.loadVendors();
  },
  methods: {
        loadVendors() {
          axios.get('/vendor/vendors')
            .then(response => {
              this.vendors = response.data;
              this.loadServiceTypesForVendors();
            })
            .catch(error => {
              console.error(error);
            });
        },
        loadServiceTypesForVendors() {
          this.vendors.forEach(vendor => {
            axios.get('/vendor/service-types/', {
              params: {
                vendor_id: vendor.id
              }
            })
              .then(response => {
                vendor.serviceTypes = response.data;
                console.log(vendor.serviceTypes)
              })
              .catch(error => {
                console.error(error);
              });
          });
        },
        loadServices(vendor) {
          axios.get('/vendor/services/', {
            params: {
              service_type_id: vendor.serviceType
            }
          })
          .then(response => {
              vendor.services = response.data.map(service => ({
                id: service.id,
                name: service.name,
                selected: false
              }));
            })
            .catch(error => {
              console.error(error);
            });
        }
      }
    };
</script>

<style>
    table {
      width: 100%;
      border-collapse: collapse;
    }

    th, td {
      padding: 8px;
      text-align: left;
      border-bottom: 1px solid #ddd;
    }

    th:first-child, td:first-child {
      width: 20%;
    }

    th:nth-child(2), td:nth-child(2) {
      width: 30%;
    }

    th:nth-child(3), td:nth-child(3) {
      width: 40%;
    }

    th:last-child, td:last-child {
      width: 10%;
    }


  </style>
