    <h2 id="how-to-get">How to get a Google Maps API key?</h2>

    <p>Getting an API key is not complicated and just requires a couple of minutes of your time. In the process, you will need to create a billing account or use credentials of already existing one, which will be used for payment, in case you exceed your limits. So be prepared to have your billing data with you. Following 3 steps, described below, you will get your API key.</p>

 

    <h3>Step 1 – Enable Billing</h3>

 

    <p>1. To start the procedure, go to <a href=https://cloud.google.com/maps-platform/ target="_blank" rel="nofollow noopener noreferrer">Google Maps Platform</a>, press “Get Started”.</p>

 

