<template>
  <div
    class="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-blueGray-100 border-0"
  >
    <div class="flex-auto px-4 lg:px-10 py-10 pt-0">
      <form>
        <h6 class="text-blueGray-400 text-sm mt-3 mb-4 font-bold uppercase">
          Mapchi Information
        </h6>
        <div v-if="!userPk" class="flex flex-wrap">
          <div class="w-full lg:w-6/12 px-4">
            <div class="relative w-full mb-3">
              <label
                class="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Mapchi-Key
              </label>
              <input
                type="text"
                required
                class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="00000000-0000-0000-0000-000000000000 *"
                maxlength="36"
                v-model="mapchi_key"
                autofocus
              />
            </div>
          </div>
          <div class="w-full lg:w-6/12 px-4">
            <div class="relative w-full mb-3">
              <label
                class="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Website address
              </label>
              <input
                type="url"
                required
                class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="https://www.mapchi.com *"
                v-model="websiteaddress"
                :disabled="!editWebsiteAddress"
              />
            </div>
          </div>
        </div>
        <div v-else class="flex flex-wrap">
          <div class="w-full lg:w-6/12 px-4">
            <div class="relative w-full mb-3">
              <label
                class="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Mapchi-Key
              </label>
              <input
                type="text"
                required
                class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="00000000-0000-0000-0000-000000000000 *"
                maxlength="36"
                v-model="mapchi_key"
                disabled
              />
              <!--:type="showPassword ? 'text' : 'password'"-->
              <!--<button class="z-10 h-full leading-snug font-normal text-center text-blueGray-600 absolute bg-transparent rounded text-base items-center justify-center w-11 right-0 pr-2 pb-5 " @click.prevent="showPassword = !showPassword">
                <i :class="showPassword ? 'fas fa-eye-slash' : 'fas fa-eye'"></i>
              </button>-->

            </div>
          </div>
          <div class="w-full lg:w-6/12 px-4">
            <div class="relative w-full mb-3">
              <label
                class="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Website address
              </label>
              <input
                type="url"
                required
                class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="https://www.mapchi.com *"
                v-model="websiteaddress"
                :disabled="!editWebsiteAddress"
                ref="websiteAddressInput"
              />
            </div>
          </div>
        </div>
        <h6 class="text-blueGray-400 text-sm mt-3 mb-4 font-bold uppercase">
          Important
        </h6>
        <div class="flex flex-wrap">
          <div class="w-full lg:w-12/12 px-4">
            <div class="relative w-full mb-3">
              <label
                class="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Mapchi-Key | Website Address
              </label>
              <textarea
                type="text"
                disabled
                class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                rows="6"
              >The MAPCHI-KEY is your unique identifier that links the widget to your website. Keeping the key private is crucial as it contains sensitive information that could compromise your account and data. The website address needs to be accurate and up-to-date to prevent widget functionality issues. It's important to note that if the MAPCHI-KEY does not match the website address, the widget won't work. Both the MAPCHI-KEY and website address are linked to a monthly paid instance, making accuracy crucial for revenue and reputation.
                  </textarea
              >
            </div>
          </div>
        </div>

        <hr class="mt-4 border-b-1 border-blueGray-300" />
        <button v-if="!userPk" class="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150" @click.prevent="saveChanges">Save</button>
        <button v-if="userPk && showEditButton" class="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150" @click.prevent="enableEditWebsiteAddress">Edit</button>
        <button v-if="userPk && showSaveButton" class="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150" @click.prevent="saveChanges">{{ saveButtonLabel }}</button>
        <button v-else-if="userPk && websiteaddress === ''" class="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150" @click.prevent="saveChanges">Save</button>

        <hr class="mt-6 border-b-1 border-blueGray-300" />
      <h6 class="text-blueGray-400 text-sm mt-3 mb-4 font-bold uppercase">
        Google Information
      </h6>
      <div class="flex flex-wrap">
          <div class="w-full lg:w-6/12 px-4">
            <div class="relative w-full mb-3">
              <label
                class="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
              Google API Key
              </label>
              <input
                type="text"
                required
                class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="000000000000000000000000000000000000000 *"
                maxlength="39"
                v-model="google_api_key"
                :disabled="!editGoogleAddress"
                ref="googleAddressInput"
              />
            </div>
          </div>
      </div>
      <h6 class="text-blueGray-400 text-sm mt-3 mb-4 font-bold uppercase">
          How to get Google Maps Api Key in 1 minute
        </h6>
        <div class="flex flex-wrap">
          <div class="w-full lg:w-12/12 px-4">
            <div class="relative w-full mb-3">
              <label
                class="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Google Maps API Key
              </label>
              <textarea
                type="text"
                disabled
                class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                rows="6"
                v-model="text"
              ></textarea>
            </div>
          </div>
        </div>
      <hr class="mt-4 border-b-1 border-blueGray-300" />

        <button v-if="userPk && showGoogleEditButton" class="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150" @click.prevent="enableEditGoogleAddress">Edit</button>
        <button v-if="userPk && showGoogleSaveButton" class="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150" @click.prevent="saveGoogleChanges">{{ saveGoogleButtonLabel }}</button>
        <button v-else-if="userPk && !google_api_key" class="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150" @click.prevent="saveGoogleChanges">Save</button>

      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import {toast} from 'bulma-toast'

export default {
  mounted() {
    this.getUserProfile()
  },
  data() {
    return {
//      showPassword: false,
      mapchi_key: '',
      websiteaddress: '',
      google_api_key: '',
      editWebsiteAddress: false,
      editGoogleAddress: false,
      userPk: null,
      text: 'In order to help our customers with Google Maps integration, we have come up with a step-by-step tutorial on how to get a Google Maps API key fast and with minimum effort. Please note Enable APIs needed for using your API key need to include, Geocoding API, Maps JavaScript API and Places API. No requirement for Directions API as per the tutorial. (https://shorturl.at/nyzWX)'
    }
  },

  computed: {
    showEditButton() {
      return this.websiteaddress && !this.editWebsiteAddress
    },

    showSaveButton() {
      return this.websiteaddress && this.editWebsiteAddress
    },

    saveButtonLabel() {
      return this.editWebsiteAddress ? 'Save' : 'Edit'
    },

    showGoogleEditButton() {
      return this.google_api_key && !this.editGoogleAddress
    },

    showGoogleSaveButton() {
      return this.google_api_key && this.editGoogleAddress
    },

    saveGoogleButtonLabel() {
      return this.editGoogleAddress ? 'Save' : 'Edit'
    }
    
  },

  methods: {
    async getUserProfile() {
      this.$store.commit('setIsLoading', true)

        axios.get('/mapche-api/v1/userprofile')
          .then(response => {
            if(response.data[0]) {
              this.mapchi_key = response.data[0].mapche_key
              this.websiteaddress = response.data[0].website_address
              this.google_api_key = response.data[0].google_api_key
              this.userPk = response.data[0].id
              this.editWebsiteAddress = false
              if (this.google_api_key) {
              this.editGoogleAddress = false
              } else {
                this.editGoogleAddress = true
              }
            } else {
             this.editWebsiteAddress = true
             this.editGoogleAddress = false
            }
          })
          .catch((error) => {
            toast({
              message: 'An error occurred. Please try again later.',
              type: 'is-danger',
              dismissible: true,
              pauseOnHover: true,
              duration: 5000,
              position: 'bottom-right',
            })
          })

      this.$store.commit('setIsLoading', false)    
    },

    enableEditWebsiteAddress() {
      this.editWebsiteAddress = true
      this.$nextTick(() => {
        this.$refs.websiteAddressInput.focus()
      })
    },

    enableEditGoogleAddress() {
      this.editGoogleAddress = true
      this.$nextTick(() => {
        this.$refs.googleAddressInput.focus()
      })
    },

    saveChanges() {
//    this.validateWebsiteAddress()

      if (!this.userPk) {
        //send POST request to create a new record
        if (!this.validateMapchiKey(this.mapchi_key)) {
          toast({
                  message: 'The MAPCHI-KEY is invalid. Use the mapchi-key on your website',
                  type: 'is-danger',
                  dismissible: true,
                  pauseOnHover: true,
                  duration: 5000,
                  position: 'bottom-right',
                })
          return;
        }
        if (!this.validateWebsiteAddress(this.websiteaddress)) {
          toast({
                  message: 'The Website URL is incorrect, expecting http:// or https://',
                  type: 'is-danger',
                  dismissible: true,
                  pauseOnHover: true,
                  duration: 5000,
                  position: 'bottom-right',
                })
          return;
        }
        axios.post('/mapche-api/v1/userprofile/', {
          mapche_key: this.mapchi_key,
          website_address: this.websiteaddress,
        })
        .then(response => {
          this.editWebsiteAddress = false
            toast({
                  message: 'Mapchi Information, updated successfully',
                  type: 'is-success',
                  dismissible: true,
                  pauseOnHover: true,
                  duration: 5000,
                  position: 'bottom-right',
                })
          this.getUserProfile()
          this.enableEditGoogleAddress()
        })
        .catch((error) => {
          if (error.response && error.response.data) {
            if (error.response.data.website_address) {
              const websiteErrorMessage = error.response.data.website_address[0];
              toast({
                message: websiteErrorMessage,
                type: 'is-danger',
                dismissible: true,
                pauseOnHover: true,
                duration: 5000,
                position: 'bottom-right',
              })
              this.websiteaddress = ''
            }
            if (error.response.data.mapche_key) {
              const mapcheKeyErrorMessage = error.response.data.mapche_key[0];
              toast({
                message: mapcheKeyErrorMessage,
                type: 'is-danger',
                dismissible: true,
                pauseOnHover: true,
                duration: 5000,
                position: 'bottom-right',
              })
              this.mapchi_key = ''
            }
          } else {
            toast({
              message: 'An error occurred. Please try again later.',
              type: 'is-danger',
              dismissible: true,
              pauseOnHover: true,
              duration: 5000,
              position: 'bottom-right',
            })
            this.mapchi_key = ''
            this.websiteaddress = ''
          }
          })

      } else {
        //send PATCH request to update website address
        axios.patch(`/mapche-api/v1/userprofile/${this.userPk}/update_website_address/`, {
          website_address: this.websiteaddress,
        })
        .then(response => {
          toast({
                  message: 'Website Address, updated successfully',
                  type: 'is-success',
                  dismissible: true,
                  pauseOnHover: true,
                  duration: 5000,
                  position: 'bottom-right',
                })
          this.editWebsiteAddress = false
          if (!this.google_api_key) {
            this.enableEditGoogleAddress()
          }
        })
        .catch((error) => {
          if (error.response && error.response.data) {
            if (error.response.data.website_address) {
              const websiteErrorMessage = error.response.data.website_address[0];
              toast({
                message: websiteErrorMessage,
                type: 'is-danger',
                dismissible: true,
                pauseOnHover: true,
                duration: 5000,
                position: 'bottom-right',
              })
              this.websiteaddress = ''
              this.getUserProfile()
            }
          } else {
            toast({
              message: 'An error occurred. Please try again later.',
              type: 'is-danger',
              dismissible: true,
              pauseOnHover: true,
              duration: 5000,
              position: 'bottom-right',
            })
            this.mapchi_key = ''
            this.websiteaddress = ''        
            }
        })
      }
    
    },

    saveGoogleChanges() {

      if (this.userPk) {
        if (!this.validateGoogleAPIKey(this.google_api_key)) {
          toast({
                  message: 'The GOOGLE API KEY is invalid',
                  type: 'is-danger',
                  dismissible: true,
                  pauseOnHover: true,
                  duration: 5000,
                  position: 'bottom-right',
                })
          return;
        }
        //send PATCH request to update UserProfile with google api key
        axios.patch(`/mapche-api/v1/userprofile/${this.userPk}/update_google_api_key/`, {
          google_api_key: this.google_api_key,
        })
        .then(response => {
          toast({
                  message: 'Google Key, updated successfully',
                  type: 'is-success',
                  dismissible: true,
                  pauseOnHover: true,
                  duration: 5000,
                  position: 'bottom-right',
                })
          this.editGoogleAddress = false
        })
        .catch((error) => {
          if (error.response && error.response.data) {
            if (error.response.data.google_api_key) {
              const websiteErrorMessage = error.response.data.google_api_key[0];
              toast({
                message: websiteErrorMessage,
                type: 'is-danger',
                dismissible: true,
                pauseOnHover: true,
                duration: 5000,
                position: 'bottom-right',
              })
              this.google_api_key = ''
              this.getUserProfile()
            }
          } else {
            toast({
              message: 'An error occurred. Please try again later.',
              type: 'is-danger',
              dismissible: true,
              pauseOnHover: true,
              duration: 5000,
              position: 'bottom-right',
            })
            this.google_api_key = ''
            }
        })

      }
    },

    validateGoogleAPIKey(key) {
      const regex = /^[a-zA-Z0-9]{39}$/;
      return regex.test(key)
    },

    validateMapchiKey(key) {
      const regex = /^[a-zA-Z0-9]{8}-[a-zA-Z0-9]{4}-[a-zA-Z0-9]{4}-[a-zA-Z0-9]{4}-[a-zA-Z0-9]{12}$/;
      return regex.test(key)
    },

    validateWebsiteAddress(address) {
      //const regex = /^(http:\/\/|https:\/\/)/;
      const regex = /^(http|https):\/\/[^ "]+$/;
      return regex.test(address);
    },
  }
}
</script>
